.container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px; 
  }
  .cont{
    width: 90%;
    margin:  0 auto;
  }







.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  color: #2c3e50; 
}

.nav-logo{
  width: 100px;
}

.nav-links {
  display: flex;
}

.nav-link {
  margin-left: 20px;
  font-size: 18px;
  text-decoration: none;
  color: #2c3e50; 
}











.card {
    position: relative; 
    margin: 10px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    width: 250px;
    height: 415px;
    box-sizing: border-box;
    -webkit-box-shadow: 0px 0px 11px 1px rgba(34, 60, 80, 0.22);
    -moz-box-shadow: 0px 0px 11px 1px rgba(34, 60, 80, 0.22);
    box-shadow: 0px 0px 11px 1px rgba(34, 60, 80, 0.22);
  }
  .card-text {
    color: #2c3e50; 
    text-align: center;
  }
  .card-image {
    max-width: 100%;
    max-height: 300px;
    object-fit:fill;
    border-radius: 5px;
  }
  .input {
    grid-column: span 4;
    margin-bottom: 20px;
    width: 20%;
    padding: 8px;
    font-size: 16px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  .card p {
    text-align: center;
    font-size: 12.9px;
    padding: 10px;
  }
  .card button {
    position: absolute; 
    bottom: 10px; 
    right: 10px; 
   
    padding: 8px;
    font-size: 14px;
    background-color: #3498db;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }




